<template>
  <div>
    <navMedic/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Listado de Pacientes</b>
      </h1>
      <h5 class="text-center">del Médico {{medic.nombre}} {{medic.apellido}}</h5>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
              <b-input-group-append>
                <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- Seleccione --</option>
              </b-form-select>
              <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table
        id="medic-patients"
        show-empty
        hover
        stacked="xl"
        fixed
        empty-filtered-text="No se encontrarón registros en su búsqueda"
        empty-text="No hay datos para mostrar"
        :items="medic.pacientes"
        :fields="columns"
        :total-rows="rows"
        :filter="filter"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        @filtered="onFiltered"
      >
        <template slot="tipoDocumento" slot-scope="data">{{data.value.descripcion}}</template>
      </b-table>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navMedic from "@/components/medic/navMedic.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "pacientesDeMedico",
  data() {
    return {
      id: this.$route.params.id,
      columns: [
        {
          key: "tipoDocumento",
          label: "Tipo Documento"
        },
        {
          key: "numDocumento",
          label: "Numero Documento"
        },
        {
          key: "nombre",
          label: "Nombre",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "apellido",
          label: "Apellido",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "domicilio"
        },
        {
          key: "obraSocial",
          label: "Obra Social"
        }
      ],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      cont: null,
      medic: []
    };
  },
  methods: {
    async findById() {
      axios
        .get(`${URL}/api/medico/${this.id}`)
        .then(response => (this.medic = response.data))
        .catch(error => {
          bootbox.alert({
            title: "Error",
            message: error.response.data.mensaje,
            backdrop: true,
            locale: "es",
            callback: this.$router.push("/medicos")
          });
          console.log("Error: " + error.response.status + " Not Found");
        });
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    },
    rows() {
      return (this.totalRows = this.findById().length);
    }
  },
  components: {
    navMedic,
    Footer
  },
  mounted() {
    this.findById();
  }
};
</script>

